import wyFetch from '../modules/wyFetch'

// 发送短信接口
export async function onSendMessage (data) {
  const myInit = {
    method: 'POST',
    credentials: 'include',
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded',
    },
    body: data
  }
  const url = '/api/app/sendValidationCode'
  const response = await wyFetch(url, myInit)
  const result = response || {code: -1, data: false, msg: '网络错误，校验失败！'}
  return result
}

// 登录
export async function onLogin (data) {
  const myInit = {
    method: 'POST',
    credentials: 'include',
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded',
    },
    body: data
  }
  const url = '/api/app/login'
  const response = await wyFetch(url, myInit)
  const result = response || {code: -1, data: false, msg: '登录失败！'}
  return {
    'code': 0,
    'data': {
      'loginId': 'sunlz',
      'userId': 17734,
      'token': 'eyJhbGciOiJIUzUxMiJ9..1JdmP794ilpZf1A84-r4tREBo7x5sOSi3Wiy5sUou5643zCLR4QAdtrW5A5jcjiXklHvWiscpFCaF75vfSV9Pg'
    },
    'msg': ''
  }
  return result
}

