// 获取登录信息
export async function getUserInfo () {
  const myInit = {
    method: 'POST',
    credentials: 'include',
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded',
    }
  }
  const url = '/api/app/getUserInfo'
  const response = await wyFetch(url, myInit)
  const result = response || {code: -1, data: false, msg: '登录失败！'}
  return result
}


