import React, { Component } from 'react'
import {
  BrowserRouter as Router,
  Route,
  Switch,
  Redirect
} from 'react-router-dom'
import ReactDOM from 'react-dom'
import './index.css'
import 'antd-mobile/dist/antd-mobile.css'
import './font/iconfont.css'
import 'antd/dist/antd.css'
import Login from './pages/Login'
import Detail from './pages/Detail'
import * as serviceWorker from './serviceWorker'
import { getUserInfo } from './pages/request'

class App extends React.Component {
  constructor (props) {
    super(props)
    this.state = {
      token: true,
    }
    // getUserInfo().then(v => {
    //
    // })
  }

  render () {
    // let a = window.localStorage.getItem('token')
    // let token = this.getCookie('token')
    return (
      <Router>
        <Switch>
          <Route path='/' exact render={props => this.state.token ? <Detail {...props} /> : <Login {...props} />}/>
        </Switch>
      </Router>
    )
  }

  getCookie (c_name) {
    if (document.cookie.length > 0) {
      let start = document.cookie.indexOf(c_name + '=')
      if (start !== -1) {
        start = start + c_name.length + 1
        let end = document.cookie.indexOf(';', start)
        if (end === -1) end = document.cookie.length
        return document.cookie.substring(start, end)
      }
    }
    return ''
  }
}

ReactDOM.render(<Detail />, document.getElementById('root'))

serviceWorker.unregister()
