import { Toast } from 'antd-mobile'

export default async function wyFetch (...args) {
  const response = await fetch(...args)
  if (!response.ok) {
    Toast.info('网络错误，请检查！', 1.5)
    return
  }
  try {
    const result = await response.json()
    let flag = result.message && (result.message).indexOf('未登录') !== -1
    if (result.code === -1 && (result.msg === 'notLogin' || flag)) {
      Toast.info('未登录，请重新登录！', 1.5)
      // 返回结果，可处理跳转
      return result
    }
    return result
  } catch (e) {}
}
