import moment from 'moment'
import React, { Component } from 'react'

let tools = {

  // 格式化时间 2018-08-31 01:12:03
  timeStampFormatToStr (ts) {
    if (!ts)
      return ''
    if (typeof ts === 'string')
      return ts
    const d = new Date(ts)
    const ydm = [d.getFullYear(), d.getMonth() + 1, d.getDate()].map(el => String(el).padStart(2, 0)).join('-')
    const hms = [d.getHours(), d.getMinutes(), d.getSeconds()].map(el => String(el).padStart(2, 0)).join(':')
    return ydm + ' ' + hms
  },

  // 深拷贝：目录数组
  deepClone (initial, final) {
    let arr = final || []
    for (let i = 0; i < initial.length; i++) {
      if (initial[i].hasOwnProperty('infoVos')) {
        arr[i] = {...initial[i]}
        arr[i]['infoVos'] = []
        tools.deepClone(initial[i]['infoVos'], arr[i]['infoVos'])
      } else {
        arr[i] = {...initial[i]}
      }
    }
    return arr
  },

  // 拆分数据（维度在前）
  getDimMergeDate (list, type) {
    let listAll = []
    let dimName = []
    let unit = type === 'd' ? '日' : (type === 'w' ? '周' : '月')
    // 拆分
    list.forEach(el => {
      if (dimName.indexOf(el.dimValue) === -1) {
        dimName.push(el.dimValue)
      }
      el['infoTopicProVos'].forEach(key => {
        key['infoTargetProVos'].forEach((item, index) => {
          listAll.push({
            mergeSec: index === 0 ? key['infoTargetProVos'].length : 0,
            dimValue: el.dimValue,
            topicId: key.topicId,
            topicName: key.topicName,
            targetId: item.targetId,
            hasAuth: item.hasAuth,
            targetName: item.targetName,
            comment: item.comment,
            targetComment: item.targetComment,
            dimId: item.dimId,
            valueMap: item.valueMap,
          })
        })
      })
    })
    // 添加唯一的key、绑定数据
    listAll.forEach((el, index) => {
      el.key = index
      Object.keys(el.valueMap).forEach(element => {
        el[element + unit] = el.valueMap[element]
      })
    })
    dimName.forEach(el => {
      let len = listAll.filter(key => key.dimValue === el).length
      let item = listAll.find(key => key.dimValue === el)
      if (item) {
        item.mergeDim = len
      }
    })
    return listAll
  },

  // 拆分数据（主题在前）
  getMergeDate (list, type) {
    let listAll = []
    let themeName = []
    // 拆分
    list.forEach(el => {
      if (themeName.indexOf(el.topicName) === -1) {
        themeName.push(el.topicName)
      }
      el['targetVos'].forEach(key => {
        key['valueVos'].forEach((item, index) => {
          listAll.push({
            mergeTarget: index === 0 ? key['valueVos'].length : 0,
            infoId: el.infoId,
            topicId: el.topicId,
            topicName: el.topicName,
            infoTopicId: key.infoTopicId,
            hasAuth: key.hasAuth,
            targetId: key.targetId,
            targetName: key.targetName,
            targetComment: key.targetComment,
            dimId: key.dimId,
            dimValue: item.dimValue,
            valueMap: item.valueMap,
          })
        })
      })
    })
    let unit = type === 'd' ? '日' : (type === 'w' ? '周' : '月')
    // 添加唯一的key、绑定数据
    listAll.forEach((el, index) => {
      el.key = index
      Object.keys(el.valueMap).forEach(element => {
        el[element + unit] = el.valueMap[element]
      })
    })
    themeName.forEach(el => {
      let len = listAll.filter(key => key.topicName === el).length
      let item = listAll.find(key => key.topicName === el)
      if (item) {
        item.mergeTheme = len
      }
    })
    return listAll
  },

  // 动态获取表头
  getColumsFromDate (startValue, endValue, type) {
    let time = type === 'd' ? 'days' : (type === 'w' ? 'weeks' : 'months')
    let format = type === 'd' ? 'YYYY-MM-DD' : (type === 'w' ? 'YYYYw' : 'YYYY-MM')
    let unit = type === 'd' ? '日' : (type === 'w' ? '周' : '月')
    let start = moment(startValue, format)
    let end = moment(endValue, format)
    let range = end.diff(start, time)
    let dateRange = []
    for (let i = 0; i <= range; i++) {
      let item = moment(start).subtract(-i, time).format(format)
      dateRange.push({
        title: item,
        dataIndex: item + unit,
        align: 'right',
        width: 100,
        render: (text, record) => record.hasAuth ? (text === undefined ? '暂无' : text) :
          <span style={{color: '#ccc'}}>无权限</span>,
      })
    }
    return dateRange
  },

  // 对象数组排序
  sortArry (list) {
    let arr = [...list]
    let compare = function (obj1, obj2) {
      let val1 = obj1.sort
      let val2 = obj2.sort
      if (val1 < val2) {
        return -1
      } else if (val1 > val2) {
        return 1
      } else {
        return 0
      }
    }
    return arr.sort(compare)
  },

  // 拖动排序
  reorder (list, startIndex, endIndex) {
    const result = Array.from(list)
    const [removed] = result.splice(startIndex, 1)
    result.splice(endIndex, 0, removed)
    return result
  },

}

export default tools
