// 计时器
import React, { Component } from 'react'
import { Button } from 'antd-mobile'

export default class TimerButton extends Component {

  constructor (props) {
    super(props)
    this.state = {
      timerCount: this.props.timerCount || 60,  // 倒计时时间
      timerTitle: this.props.timerTitle || '获取验证码', // 倒计时title
      counting: false, // 计时中
      selfEnable: true, // 是否可用
    }
    this.shouldStartCountting = this.shouldStartCountting.bind(this)
    this.countDownAction = this.countDownAction.bind(this)
  }

  // 倒计时
  countDownAction () {
    const codeTime = this.state.timerCount
    this.interval = setInterval(() => {
      const timer = this.state.timerCount - 1
      if (timer === 0) {
        this.interval && clearInterval(this.interval)
        this.setState({
          timerCount: codeTime,
          timerTitle: this.props.timerTitle || '获取验证码',
          counting: false,
          selfEnable: true
        })
      } else {
        this.setState({
          timerCount: timer,
          timerTitle: `已发送(${timer}s)`,
        })
      }
    }, 1000)
  }

  // 是否开始计时
  shouldStartCountting (shouldStart) {
    if (this.state.counting) {
      return
    }
    if (shouldStart) {
      this.countDownAction()
      this.setState({counting: true, selfEnable: false})
    } else {
      this.setState({selfEnable: true})
    }
  }

  // 清除计时器
  componentWillUnmount () {
    clearInterval(this.interval)
  }

  render () {
    const {onclick, enable, check} = this.props
    const {counting, timerTitle, selfEnable} = this.state
    return (
      <Button type="ghost"
              disabled={!(!counting && selfEnable) || !enable}
              onClick={() => {
                if (enable && !counting && selfEnable && check) {
                  this.setState({selfEnable: false})
                  this.shouldStartCountting(true)
                }
                onclick()
              }}
              inline size="small" style={{marginRight: '4px'}}>
        {timerTitle}
      </Button>
    )
  }
}

